import React from 'react'
import { Grid, Typography } from '@mui/material'
import { Test } from '../types'
import { msToTime, formatUserfacingTestId as formatId, toDateTime } from '../../../lib/StringUtils'
import { NameValue } from './NameValue'
import { errorGradient, successGradient } from '../../../styles/colors'

interface Props {
  test: Test
  failStepRef: any
}

export const TestInfo = (props: Props) => {
  const { test, failStepRef } = props
  // latest testRun, assuming testRuns are sorted by date ASC (which should always be the case)

  const testRun = test?.testRuns[test?.testRuns.length - 1]
  console.log('test.testRuns', test.testRuns)
  const packageData = testRun?.appVersions[0]
  const stepResults = testRun?.stepResults

  const successSteps = stepResults.filter(result => result.success)
  const failedSteps = stepResults.filter(result => !result.success)
  const isFailedTest = failedSteps.length > 0
  const isDeviceSame = test.deviceData.serial === testRun.deviceData.serial // is test recorded & executed on same device

  const scrollToFailStep = React.useCallback(() => {
    failStepRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
  }, [failStepRef])

  React.useEffect(() => {
    const scrollContainer = document.getElementById('scrollContainer')
    if (isFailedTest) {
      setTimeout(() => scrollToFailStep(), 600) // delay scroll so images get loaded
      return
    }
    scrollContainer?.scrollTo({ top: 0, left: 0, behavior: 'auto' }) // if not failed step then scroll to top
  }, [test.id, isFailedTest, failStepRef, scrollToFailStep])

  const stepsFailedStyle = { fontSize: '1rem', textDecoration: 'auto', cursor: 'auto' }
  if (failedSteps.length > 0) {
    stepsFailedStyle.textDecoration = 'underline'
    stepsFailedStyle.cursor = 'pointer'
  }

  if (!testRun) {
    return null
  }

  return (
    <div>
      <Typography variant="h1" component="div" style={{ marginBottom: 20, marginTop: 7 }}>
        {test.title} (ID: {formatId(test.id)})
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={6}>
          <NameValue name="Test Created:" value={toDateTime(test?.createdOn)} />
          <NameValue name="Package Name:" value={packageData?.packageName} />
          <NameValue name="Test Run Duration:" value={msToTime(testRun?.duration)} />
          <NameValue name="App Min Sdk:" value={packageData?.minSdk} />
          <NameValue name="Install Date:" value={toDateTime(packageData?.firstInstallTime)} />
        </Grid>
        <Grid item xs={6}>
          <NameValue name="Test Run Date:" value={toDateTime(testRun?.date)} />
          <NameValue name="Package Version:" value={packageData?.name} />
          <NameValue name="App Code:" value={packageData?.code} />
          <NameValue name="App Target Sdk:" value={packageData?.targetSdk} />
          <NameValue name="Last Update Date:" value={toDateTime(packageData?.lastUpdateTime)} />
        </Grid>
      </Grid>
      <Grid container spacing={3} style={{ marginBottom: 20 }}>
        <Grid item xs={isDeviceSame ? 12 : 6}>
          <NameValue
            name={`Device ${isDeviceSame ? '' : '(Recorded)'}:`}
            value={test?.deviceData?.name + ' / ' + test?.deviceData.os + ' ' + (test?.deviceData?.osVersion || '')}
          />
        </Grid>
        {!isDeviceSame && (
          <Grid item xs={6}>
            <NameValue
              name="Device (Executed):"
              value={testRun?.deviceData?.name + ' / ' + testRun?.deviceData.os + ' ' + (testRun?.deviceData?.osVersion || '')}
            />
          </Grid>
        )}
      </Grid>

      <div
        style={{
          borderRadius: 4,
          padding: 10,
          background: testRun.wasSuccessful ? successGradient : errorGradient
        }}
      >
        <Typography sx={{ fontSize: '1rem' }} variant="h6">
          {`${test.stepsCountWithSubTestSteps} steps: ${successSteps.length} successful / `}
          <Typography component="span" sx={stepsFailedStyle} variant="h6" onClick={() => (failedSteps.length > 0 ? scrollToFailStep() : null)}>
            {`${failedSteps.length}  failed`}
          </Typography>
        </Typography>

        {isFailedTest && (
          <Typography sx={{ fontSize: '1rem', whiteSpace: 'break-spaces' }} variant="h6">
            Status Message:{` ${failedSteps[0]?.message}`}
          </Typography>
        )}
      </div>
    </div>
  )
}
